.card-list {
    max-width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}

@media (max-width: 1024px) {
    .card-list {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media (max-width: 768px) {
    .card-list {
        grid-template-columns: 1fr 1fr;
    } 
}

@media (max-width: 512px) {
    .card-list {
        grid-template-columns: 1fr;
    }
}